
.container-data{
    max-width: 50%;
    border: 1px solid black;
    border-radius: 1%;
    margin-right: 10px;
}
.container-fluid{
    margin-bottom: 10rem;
}
.print{
    position:fixed;

}
.print button{
    font-size: 30px;
    transition: 0.5s !important;
    background-color: rgba(0, 255, 255, 0.12) !important;
}
.print button:hover{
    color: red;
    background-color: aqua;
    rotate: 20deg;
    font-size: 33px;
    margin-top: -0.3rem;
    rotate: -20deg;
    border-radius: 70%;
}
  
.table_Data tbody tr:nth-of-type(even) {
    background-color: white !important; 
    color: black;
}
.table_Data tbody tr:nth-of-type(odd) {
    background-color: rgb(158, 183, 183);
    color: black;
}
thead{
    border: 2px solid black;
}
@Page{
    size: A4;
}
@media print {
    body ,.container-fluid,.container-data,
    h4,
    table,
    tr,
    td{
         /* transform: scaleX(1.1);  */
        font-size: 12px !important;
    }
    body{
        max-height: 100% !important;
    }
    nav{
        visibility: hidden !important;
    }
    .print{
      display: none;
    }
    .container-fluid{
        justify-content: center !important;
        max-height: 100% !important;
    }
    .container-data{
        display: block !important;
    }
    .table_Data{
        justify-self: center !important;
    }
    .printing-footer{
        visibility: visible !important;
    }
    nav{
        visibility: hidden;
        position: absolute !important;
    }   
    nav a{
        visibility: visible;
    }
    footer{
        visibility: hidden !important;
    }
  }