.checkbox{
    background-color: white;
    border: 1px dashed var(--main-color);
    transition: 0.5s;
}
.checkbox:checked{
    background-color: var(--main-color) !important;
    border-radius: 50%;
}
.user_pass_icon{
    font-size: 40px;
    color: var(--main-color);
    padding-right: 2px;
}

@media only screen and (min-width: 1000px){
    .container{
        max-width: 50% !important;
    }
}