@import url('https://fonts.googleapis.com/css2?family=Noto+Kufi+Arabic:wght@300;500&display=swap');

:root{
    --maian-bg-color:rgb(143, 134, 134) ;
    --main-color:rgb(116, 58, 57) ;
}
*{
    text-shadow: 1px;
}
.blur{
    filter: blur(7px);
}
.notblur{
filter: blur(0px);
}
body{
    transition: 0.5s !important;
    background-color: rgb(245, 245, 245) !important;
}
.flip{
    transform: rotateY(180deg);
}
input::placeholder{
    color: rgba(59, 63, 59, 0.751) !important;
}
input:focus,a:focus{
    border:1px solid rgb(0, 0, 0) !important;
}
.Main-Them{
    background-color: var(--maian-bg-color) !important;
    color: var(--main-colors) !important;
}
.user_icon{
    transition: 1.2s;
    cursor: pointer;
}
.user_icon:hover{
    color: black;
}
.user_profile{
    font-size: 50px !important;
}
.infos-div{
    
    background-color: #fff !important;
    border-radius: 5px;
    margin-right: 2vw;
    margin-top: 14vh !important;
    position: fixed;
}
.btn-Main{
    color: rgb(18, 18, 18);
    border: 2px solid var(--main-color);
    transition: 0.5s !important;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
}

.btn-Main:hover{
    background-color: var(--main-color) !important;
    color: white !important;
    border: 1px solid rgb(9, 255, 9);
}
.btn-Login{
    border: 1px solid yellow;
    transition: 0.6s;
}
.btn-Login:hover{
    background-color: green;
    color: white !important;
}
.loading_effect{
    color: var(--main-color);
    /* color: white !important; */
    text-align: center !important;
    margin-top: 20%;
    font-size: 50px !important;
    transition: 0.5s;
    animation: rotateAnimation 0.4s linear infinite;
}
  @keyframes rotateAnimation {
    from {
        display: none;
      transform: rotate(0deg); 
    }
    to {
        display: block;
      color: black;
      transform: rotate(360deg); /* Ending point of rotation (360 degrees) */
    }
  }




nav{
    opacity: 80% ;
    transition: 0.7s;
}
nav:hover{
    opacity: 90%;
}
.Main-con-Them{
    /* background-color:var(--maian-bg-color); */
    color: black;
    padding-top: 10px !important;
    margin-bottom: 10px !important;
}

.contact-icons img {
    opacity:0.4 !important;
    width: 40px; /* Adjust the width and height according to your preference */
    height: 40px;
    border-radius: 50%; /* Create a circular shape */
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); /* Add a subtle shadow effect */
    padding-top: -50px !important;
    padding-bottom: -50px !important;
    top: -20px !important;
}
.contact-icons img:hover{
    filter: hue-rotate(60deg) saturate(2);
    opacity:1 !important;
}
.social_icon{
    position: absolute;
    right: 20px;
    top: -45px;
}
footer{
    z-index: 100 !important;
}
.Sm-Screans-Footer{
    margin-top: 30% !important;
    margin-bottom: 0px !important;
    margin-left: 0px !important;
    margin-right: 0px !important;

} 
.controllers{
    display: flex;
    justify-content: space-between;
}
.container{
    margin-top: 90px !important;
    padding-bottom: 80px;
    width: 80% !important;
    border-radius: 5%;
    opacity: 90%;
    transition: 0.3s;

    font-family: 'Noto Kufi Arabic', sans-serif;

}
.container:hover{
    opacity: 93%;
}
#myCarousel{
    width: 80% !important;
    height: 70% !important;
    margin: auto;

}
.carousel-item img{
    margin: auto;
    justify-self: center !important;
    border-radius: 3% !important;
    height:20% !important;
}
.navbar-brand{
    padding-left: 4%;
    font-size: 16px !important;
}

.form-group input ,select{
    background-color: var(--main-bg-color) !important;
    color: var(--main-color) !important;
    border: 1px solid var(--main-color) !important;
    transition: 0.5s !important;
    font-size: 20px;
}
.form-group input:hover,input:focus ,select:hover{
    background-color: var(--main-bg-color) !important;
    color: var(--main-color) !important;
    border: 3px solid var(--main-color) !important;
}
.To-Right{
    text-align: right !important;
    direction:rtl !important;
}
.RTL{
    direction:rtl !important;
}
.im{
    height: 30% !important;
}

@media only screen and (min-width: 800px){
    .container{
        max-width: 50% !important;
    }
    .btn-Main{
        max-width:100%;
        margin: auto;
    }
}

.bg-aquas{
    background-color: rgba(74, 147, 147, 0.164);
    border-radius: 4%;
    height:auto ;
}
.btnAddFunc{
    text-align:left !important;
    direction:ltr !important;
    border-radius: 150%;
    max-width: 35px;
    max-height: 35px;
}

.funcs{
    margin-left: 3px;
    border: 1px solid rgba(74, 147, 147, 0.164);;
    border-radius: 20% !important;
}
.active-func{
    border-top: 1px solid var(--main-color) !important;
    border-left: 1px solid var(--main-color) !important;
    border-right: 1px solid var(--main-color) !important;
    background-color: rgba(255, 255, 255, 0.511);
}
.cross{
    color: red;
    font-weight: 300;
    margin-top: 7px !important;
}
.icon-edit{
    font-size: 24px;
    transition: 0.3s;
    cursor: pointer;
}
.icon-edit:hover{
    color:rgb(126, 126, 177);
    rotate: -3deg;
    top: -2px;
}
.icon-delete{
    cursor: pointer;
    transition: 0.3s;
    font-size: 24px;
}
.icon-delete:hover{
    color: rgb(255, 0, 0);
    rotate: 25deg;
}

.ifrm_dg{
    min-width: 100% !important;
    height: 25rem !important;
}