.container-fluid{
    margin-top: 5rem;
    transition: 0.5s;
    width: 95% !important;
    justify-content: center;
}
.tables-list{
    text-align: center;
    justify-content: center;
    margin-left:2%;
    transition: 0.5s;
}
.tables-list button{
    width: 100%;
    text-align: center;
    font-weight: bold;
}
.user-requst {
    opacity: 90%;
    transition: 0.5s;
    width: 8vw ;
}
.user-requst:hover {
    opacity: 100%;
}
.user-requst h4{
    text-align: center;
    margin-bottom: -4%;
}
.user-requst table{
    max-width: 16%;
    margin-top: 5%;
    z-index: 10;
}
table{
    cursor: pointer;
}
.center-div{
    margin-right: -10% !important;
    transition: 0.5s;
    margin-bottom: 5%;
}
.center-div table{
    max-width: 80% !important;
}




.scrollable-container {
    height: 14vh;
    width: 45vw; /* Set your desired width */
    white-space: pre-line; /* Prevent text from wrapping */
    overflow-x: scroll; /* Enable horizontal scrolling */
    border: 1px solid #ccc; /* Add a border for visualization */
  }
  .scrollable-content select ,.scrollable-content option{
    color: white !important;
    width: 15rem;
  }
  .Selection_content{
    display: block !important;
  }
  .scrollable-content {
    color: white !important;
    display: block;/* Display content in a row */
    width: 1000px; /* Set the total width of the content */
    background-color: black; /* Add a background color for visualization */
    padding: 4px; /* Add some spacing within the content */
  }


@media screen and (min-width: 1040px) {
    .tables-list{
        /* position: fixed; */
    }
    .tables-list button{
         width: 18vw !important;
    }
    .container-fluid{
        display: flex;
        justify-content:center;
    }
}  

@media print {
    *{
        background-color: white !important;
        color: black !important;
    
    }
    .tables-list{
        visibility: hidden;
    }
    nav{
        visibility: hidden;
    }
    .scrollable-container{
        width: 80%;
        height: 250rem;
        overflow-x: visible; 
        overflow-y: scroll; 
        white-space: normal; /* Allow text to wrap for printing */
        display: block !important; 
        background-color: white !important;
        color: black !important;
        justify-content: space-between;
        margin-right: 50px;
    }
    .scrollable-content div{
        
        background-color: white !important;
        color: black !important;
        display: block;
    }
    
    .scrollable-content select ,.scrollable-content option{
        width: 50% !important;
        color: black !important;
        margin-top: 10px !important;
        margin-left: 50px !important;
    }
    .table{
        width: 100% !important;
        height: 30rem;
    }
}